'use client';

import * as React from 'react';
import { collection, onSnapshot, query, orderBy } from 'firebase/firestore';
import { useFirestore } from '@/firebase';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { getStatusStyles } from '@/lib/utils';
import { Skeleton } from "@/components/ui/skeleton";
import { Input } from "@/components/ui/input";
import { Tabs, TabsList, TabsTrigger } from "@/components/ui/tabs";
import { Eye } from 'lucide-react';
import { Button } from '@/components/ui/button';
import QueryDetailModal from './query-detail-modal';

export default function QueryTable() {
  const firestore = useFirestore();
  const [queries, setQueries] = React.useState([]);
  const [loading, setLoading] = React.useState(true);
  const [filter, setFilter] = React.useState("all");
  const [searchTerm, setSearchTerm] = React.useState("");
  const [selectedQuery, setSelectedQuery] = React.useState(null);

  React.useEffect(() => {
    if (!firestore) return;

    const q = query(collection(firestore, "queries"), orderBy("fechaCreacion", "desc"));

    const unsubscribe = onSnapshot(q, (snapshot) => {
      const allQueries = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setQueries(allQueries);
      setLoading(false);
    }, (error) => {
      console.error("Error fetching queries:", error);
      setLoading(false);
    });

    return () => unsubscribe();
  }, [firestore]);

  const filteredQueries = React.useMemo(() => {
    return queries
      .filter(query => {
        if (filter === "all") return true;
        return query.status.toLowerCase() === filter;
      })
      .filter(query => {
        const search = searchTerm.toLowerCase();
        return (
          query.expediente.toLowerCase().includes(search) ||
          query.nombreEstablecimiento.toLowerCase().includes(search)
        );
      });
  }, [queries, filter, searchTerm]);

  const handleOpenModal = (query) => {
    setSelectedQuery(query);
  };

  const handleCloseModal = () => {
    setSelectedQuery(null);
  };

  if (loading) {
    return <Skeleton className="h-96 w-full" />;
  }

  return (
    <div className="border rounded-lg bg-card text-card-foreground shadow-sm">
      <div className="p-4 flex flex-col sm:flex-row items-center justify-between gap-4">
        <Input
          placeholder="Buscar por expediente o establecimiento..."
          value={searchTerm}
          onChange={(e) => setSearchTerm(e.target.value)}
          className="max-w-sm"
        />
        <Tabs value={filter} onValueChange={setFilter} className="w-full sm:w-auto">
          <TabsList className="grid w-full grid-cols-4">
            <TabsTrigger value="all">Todos</TabsTrigger>
            <TabsTrigger value="pendiente">Pendientes</TabsTrigger>
            <TabsTrigger value="asignado">Asignados</TabsTrigger>
            <TabsTrigger value="atendido">Atendidos</TabsTrigger>
          </TabsList>
        </Tabs>
      </div>
      <div className="overflow-x-auto">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Expediente</TableHead>
              <TableHead>Establecimiento</TableHead>
              <TableHead>Tema</TableHead>
              <TableHead>Fecha</TableHead>
              <TableHead>Estado</TableHead>
              <TableHead className="text-right">Acciones</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {filteredQueries.map((query) => (
              <TableRow key={query.id}>
                <TableCell className="font-medium">{query.expediente}</TableCell>
                <TableCell>{query.nombreEstablecimiento}</TableCell>
                <TableCell>{query.tema}</TableCell>
                <TableCell>{new Date(query.fechaCreacion).toLocaleDateString()}</TableCell>
                <TableCell><Badge style={getStatusStyles(query.status)}>{query.status}</Badge></TableCell>
                <TableCell className="text-right">
                  <Button variant="ghost" size="icon" onClick={() => handleOpenModal(query)}>
                    <Eye className="h-4 w-4" />
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </div>
      {selectedQuery && <QueryDetailModal query={selectedQuery} onClose={handleCloseModal} />}
    </div>
  );
}
