'use client';

import * as React from 'react';
import { collection, onSnapshot } from 'firebase/firestore';
import { useFirestore } from '@/firebase';
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Skeleton } from "@/components/ui/skeleton";
import { BarChart, ListTodo, ClipboardCheck, Clock } from 'lucide-react';

export default function StatsCards() {
  const firestore = useFirestore();
  const [stats, setStats] = React.useState({ total: 0, pendientes: 0, asignadas: 0, atendidas: 0 });
  const [loading, setLoading] = React.useState(true);

  React.useEffect(() => {
    if (!firestore) return;

    const unsubscribe = onSnapshot(collection(firestore, "queries"), (snapshot) => {
      let total = 0;
      let pendientes = 0;
      let asignadas = 0;
      let atendidas = 0;

      snapshot.forEach(doc => {
        const data = doc.data();
        total++;
        switch (data.status) {
          case 'Pendiente':
            pendientes++;
            break;
          case 'Asignado':
            asignadas++;
            break;
          case 'Atendido':
            atendidas++;
            break;
          default:
            break;
        }
      });

      setStats({ total, pendientes, asignadas, atendidas });
      setLoading(false);
    });

    return () => unsubscribe();
  }, [firestore]);

  if (loading) {
    return (
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Skeleton className="h-28" />
        <Skeleton className="h-28" />
        <Skeleton className="h-28" />
        <Skeleton className="h-28" />
      </div>
    );
  }

  return (
    <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
      <StatCard icon={<BarChart />} title="Consultas Totales" value={stats.total} />
      <StatCard icon={<ListTodo />} title="Consultas Pendientes" value={stats.pendientes} />
      <StatCard icon={<Clock />} title="Consultas Asignadas" value={stats.asignadas} />
      <StatCard icon={<ClipboardCheck />} title="Consultas Atendidas" value={stats.atendidas} />
    </div>
  );
}

function StatCard({ icon, title, value }) {
  return (
    <Card>
      <CardHeader className="flex flex-row items-center justify-between space-y-0 pb-2">
        <CardTitle className="text-sm font-medium">{title}</CardTitle>
        {icon}
      </CardHeader>
      <CardContent>
        <div className="text-2xl font-bold">{value}</div>
      </CardContent>
    </Card>
  );
}
