'use client';

import * as React from 'react';
import { doc, getDoc, updateDoc } from 'firebase/firestore';
import { useFirestore } from '@/firebase';
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogDescription, DialogFooter } from '@/components/ui/dialog';
import { Button } from '@/components/ui/button';
import { getStatusStyles } from '@/lib/utils';
import { Badge } from '@/components/ui/badge';
import { Separator } from '@/components/ui/separator';
import { Textarea } from '@/components/ui/textarea';
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from '@/components/ui/select';
import { toast } from 'sonner';
import { Loader2, Save } from 'lucide-react';

const Personal = ['Jency', 'Miguel', 'Valeria']; // Personal al que se puede asignar
const Estados = ['Pendiente', 'Asignado', 'Atendido'];

export default function QueryDetailModal({ query, onClose }) {
  const firestore = useFirestore();
  const [userDetails, setUserDetails] = React.useState(null);
  const [loadingUser, setLoadingUser] = React.useState(true);
  const [isUpdating, setIsUpdating] = React.useState(false);

  // Estados para los campos editables
  const [asignadoA, setAsignadoA] = React.useState(query.asignadoA || '');
  const [status, setStatus] = React.useState(query.status);
  const [observacion, setObservacion] = React.useState(query.observacion || '');

  React.useEffect(() => {
    const fetchUserDetails = async () => {
      if (firestore && query.userId) {
        try {
          const userDoc = await getDoc(doc(firestore, 'users', query.userId));
          if (userDoc.exists()) {
            setUserDetails(userDoc.data());
          }
        } catch (error) {
          console.error("Error fetching user details:", error);
        }
      }
      setLoadingUser(false);
    };
    fetchUserDetails();
  }, [firestore, query.userId]);

  const handleUpdate = async () => {
    setIsUpdating(true);
    try {
      const queryRef = doc(firestore, 'queries', query.id);
      await updateDoc(queryRef, {
        asignadoA,
        status,
        observacion,
        fechaActualizacion: new Date().toISOString(),
      });
      toast.success('Consulta actualizada con éxito');
      onClose();
    } catch (error) {
      console.error("Error updating query:", error);
      toast.error('Error al actualizar. Inténtelo de nuevo.');
    }
    setIsUpdating(false);
  };

  return (
    <Dialog open={true} onOpenChange={onClose}>
      <DialogContent className="max-w-3xl">
        <DialogHeader>
          <DialogTitle className="flex items-center justify-between">
            <span>Detalle de Consulta: {query.expediente}</span>
            <Badge style={getStatusStyles(status)}>{status}</Badge>
          </DialogTitle>
          <DialogDescription>
            Fecha de Creación: {new Date(query.fechaCreacion).toLocaleString()}
          </DialogDescription>
        </DialogHeader>

        <div className="grid grid-cols-1 md:grid-cols-2 gap-6 py-4">
          {/* Columna de Información del Establecimiento y Consulta */}
          <div className="space-y-4">
            <h4 className="font-semibold">Información del Establecimiento</h4>
            <p><strong>Nombre:</strong> {query.nombreEstablecimiento}</p>
            <p><strong>Código:</strong> {query.codigoEstablecimiento || 'N/A'}</p>
            <p><strong>RUC:</strong> {query.ruc || 'N/A'}</p>
            <p><strong>Email:</strong> {query.emailEstablecimiento}</p>
            <Separator />
            <h4 className="font-semibold">Detalles de la Consulta</h4>
            <p><strong>Tema:</strong> {query.tema}</p>
            {query.descripcion && <p><strong>Descripción:</strong> {query.descripcion}</p>}
          </div>

          {/* Columna de Información del Usuario y Acciones */}
          <div className="space-y-4">
            <h4 className="font-semibold">Información del Solicitante</h4>
            {loadingUser ? <p>Cargando...</p> : userDetails ? (
              <>
                <p><strong>Nombre:</strong> {userDetails.nombres} {userDetails.apellidos}</p>
                <p><strong>DNI:</strong> {query.userId}</p>
                <p><strong>Email:</strong> {userDetails.email}</p>
                <p><strong>Celular:</strong> {userDetails.celular}</p>
              </>
            ) : <p>No se encontró al usuario.</p>}
            <Separator />
            <h4 className="font-semibold">Gestión de la Consulta</h4>
            <div className="space-y-2">
              <Label>Asignar a:</Label>
              <Select onValueChange={setAsignadoA} defaultValue={asignadoA}>
                <SelectTrigger><SelectValue placeholder="Seleccionar personal" /></SelectTrigger>
                <SelectContent>{Personal.map(p => <SelectItem key={p} value={p}>{p}</SelectItem>)}</SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label>Cambiar Estado:</Label>
              <Select onValueChange={setStatus} defaultValue={status}>
                <SelectTrigger><SelectValue placeholder="Seleccionar estado" /></SelectTrigger>
                <SelectContent>{Estados.map(e => <SelectItem key={e} value={e}>{e}</SelectItem>)}</SelectContent>
              </Select>
            </div>
            <div className="space-y-2">
              <Label>Observaciones Internas:</Label>
              <Textarea value={observacion} onChange={(e) => setObservacion(e.target.value)} placeholder="Añadir observaciones..." />
            </div>
          </div>
        </div>

        <DialogFooter>
          <Button variant="outline" onClick={onClose}>Cancelar</Button>
          <Button onClick={handleUpdate} disabled={isUpdating}>
            {isUpdating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Save className="mr-2 h-4 w-4" />}Guardar Cambios
          </Button>
        </DialogFooter>
      </DialogContent>
    </Dialog>
  );
}

// Pequeño componente Label para consistencia
function Label({ children }) {
    return <label className="text-sm font-medium leading-none peer-disabled:cursor-not-allowed peer-disabled:opacity-70">{children}</label>;
}
