'use client';

import * as React from 'react';
import { useRouter } from 'next/navigation';
import { useUser } from '@/hooks/use-auth';
import StatsCards from '@/components/admin/stats-cards';
import QueryTable from '@/components/admin/query-table';
import { LayoutDashboard } from 'lucide-react';
import { Skeleton } from '@/components/ui/skeleton';

export default function AdminDashboardPage() {
  const { user, loading } = useUser();
  const router = useRouter();

  React.useEffect(() => {
    if (!loading && !user) {
      router.push("/admin/login");
    }
  }, [user, loading, router]);

  if (loading || !user) {
    return (
      <div className="space-y-8 w-full">
        <div className="bg-primary-pattern text-primary-foreground p-4 rounded-lg shadow-md flex items-center gap-4">
          <LayoutDashboard className="h-8 w-8" />
          <div>
            <h1 className="text-3xl font-bold font-headline tracking-tight">Panel de Administración</h1>
            <p className="text-primary-foreground/80">Cargando datos del sistema...</p>
          </div>
        </div>
        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
          <Skeleton className="h-28" />
        </div>
        <Skeleton className="h-96" />
      </div>
    );
  }

  return (
    <div className="space-y-8">
      <div className="bg-primary-pattern text-primary-foreground p-4 rounded-lg shadow-md flex items-center gap-4">
        <LayoutDashboard className="h-8 w-8" />
        <div>
          <h1 className="text-3xl font-bold font-headline tracking-tight">Panel de Administración</h1>
          <p className="text-primary-foreground/80">Gestione y monitoree todas las consultas del sistema.</p>
        </div>
      </div>

      <StatsCards />

      <QueryTable />
    </div>
  );
}
